@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Roboto', sans-serif;
}

.day{
  background: linear-gradient(to right,#FF7800 ,#FFE300);
}
.night{
background: linear-gradient(to right, #C996CC, #3D2C8D)
}
main{
  min-height: 100vh;
  background-image:  linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.75));
  padding:25px;
}
.search-box{
  width: 100%;
  margin: 0 0 75px;
}
.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;
  
  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, .5);
  border-radius: 0 0 16px 16px ;
  font-size: 20px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0,0,0,0.2);
  
  color: black;
  font-size: 20px;

  transition: 0.4s ease;

}
.search-box .search-bar:focus{
  background-color: rgba(255, 255, 255, .75);
}

.location-box .location{
  color: azure;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, .5);
}
.location-box .date{
  color: azure;
  font-size: 20px;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box{
  text-align: center;
}

.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  
  padding: 15px 25px;

  color: azure;
  font-size: 102px;
  
  font-weight: 900;
  text-shadow:  3px 6px rgba(50, 50, 70, .5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, .2); ;
}

.weather-box .weather{
color: azure;
font-size: 40px;
font-weight: 700;
text-shadow:  3px 3px rgba(50, 50, 70, .5);
}
.weather{
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
}
.logo{
  padding: 15px;
}
.error{
  color: white;
  font-size: 100px;
  margin: 0;
  padding: 0;
}
.shadow{
  filter: drop-shadow( 3px 3px rgba(50, 50, 70, 0.5));
}